.field {
  position: relative;
  display: block;
  width: 100%;

  input,
  textarea {
    display: block;
    width: 100%;
    padding: 0 0 25px;
    font-size: 20px;
    line-height: 1;
    color: $white;
    border: none;

    border-bottom: solid 1px $grey-75;
    border-radius: 0;
    background-color: transparent;
    appearance: none;

    transition: border-bottom-color $transition;

    outline: none;

    @include hover {
      border-bottom-color: $white;
    }
  }

  &__hint {
    position: absolute;
    bottom: 25px;
    left: 0;
    font-size: 20px;
    line-height: 1;
    color: $grey-75;
    letter-spacing: 0.05em;

    transition: transform $transition;
  }

  &::placeholder {
    color: $grey-75;
    opacity: 1;
  }

  input:focus + &__hint,
  textarea:focus + &__hint {
    display: none;
    //transform: translateY(-100%);
  }
}
